* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, sans-serif;
  background: #617d98;
  color: #222;
}

.title {
  color: hsl(49, 100%, 50%);
  margin-top: 4rem;
  text-transform: capitalize;
  text-align: center;
}

.subtitle {
  color: hsl(49, 50%, 80%);
  text-align: center;
}

.booklist {
  width: 90vw;
  max-width: 1170px;
  margin: 5rem auto;
  display: grid;
  gap: 2rem;
}

@media (min-width: 768px) {
  .booklist {
    grid-template-columns: repeat(3, 1fr);
    align-items: start;
  }
}

.book {
  position: relative;
  background: #fff;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
}

.rank {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 1rem;
  padding: 0.75rem;
  border-top-left-radius: 1rem;
  background: #c35600;
  color: #ebebeb;
  font-weight: bold;
}

.book h2 {
  margin: 1rem 0px;
  font-size: 1rem;
}

.book img {
  max-width: 100%;
  object-fit: cover;
}

.book h4 {
  color: #617d98;
  font-size: 0.75rem;
  margin-top: 0.5rem;
  letter-spacing: 0.25rem;
}

button {
  margin: 1rem 0;
}
